<template>
	<view>
		<cu-custom bgColor="theme-bg-color" :isBack="false">
			<block slot="backText">返回</block>
			<block slot="content">自习室</block>
		</cu-custom>
		<scroll-view scroll-x class="bg-white nav">
			<view class="flex text-center">
				<view class="cu-item flex-sub" :class="index==TabCur?'text-theme cur':''"
					v-for="(item,index) in typeItem" :key="index" @tap="tabSelect" :data-id="index">
					{{item}}
				</view>
			</view>
		</scroll-view>


			<view v-if="TabCur==0">
			<view class="cu-card case" v-if="recRoomItem.length>0">
				<template v-for="(item,index) in recRoomItem">
					<view class="cu-item shadow" @click="ToDetail(item.id)">
						<view class="image">
							<image :src="item.imgUrl" mode="widthFix"></image>
							<view class="cu-tag bg-red">精选</view>
							<view class="cu-bar bg-shadeBottom"> <text class="text-cut">{{item.name}}</text></view>
						</view>
						<view class="cu-list" style="padding:20upx">
							<view class="cu-item">
								<view class="text-gray text-sm flex justify-end">

									<view class="text-gray text-sm">
										<text class="cuIcon-hot margin-lr-xs"></text>最大人数:{{item.capacity}}
									
									</view>
								</view>
							</view>
						</view>
					</view>

				</template>
			</view>
			<view v-else>
			<cu-no-data></cu-no-data>
			</view>
			</view>
			
			<view v-if="TabCur==1">
			<view class="cu-card case" v-if="roomItem.length>0">
				<template v-for="(item,index) in roomItem">
					<view class="cu-item shadow" @click="ToDetail(item.id)">
						<view class="image">
							<image :src="item.imgUrl" mode="widthFix"></image>
							<view class="cu-tag bg-red">精选</view>
							<view class="cu-bar bg-shadeBottom"> <text class="text-cut">{{item.name}}</text></view>
						</view>
						<view class="cu-list" style="padding:20upx">
							<view class="cu-item">
								<view class="text-gray text-sm flex justify-end">

									<view class="text-gray text-sm">
										<text class="cuIcon-hot margin-lr-xs"></text>最大人数:{{item.capacity}}
										<text
											class="cuIcon-locationfill margin-lr-xs"></text>开设座位:{{item.roomSeatList.length}}个

									</view>
								</view>
							</view>
						</view>
					</view>

				</template>
			</view>
			<view v-else>
				<cu-no-data></cu-no-data>
			</view>
			</view>
		
		<view style="height:120upx"></view>
		<footer-bar :currentIndex="1"></footer-bar>
	</view>
</template>

<script>
	import store from "@/store";
	import {
		mapGetters
	} from "vuex";
	export default {
		data() {
			return {
				typeItem: ["推荐自习室", "所有自习室"],

				isCard: false,
				TabCur: 0,
				roomItem: [],
                 recRoomItem:[],
				keyword: ""
			}
		},
		computed: {
			...mapGetters(["userInfo"]),
		},
		onLoad(){
			this.RecommandRoomListApi();
		},
		async onShow() {



		},
		methods: {


			async tabSelect(e) {
				this.TabCur = e.currentTarget.dataset.id;

				this.scrollLeft = (e.currentTarget.dataset.id - 1) * 60
				if (this.TabCur == 0) {
					this.RecommandRoomListApi();
				} else if (this.TabCur == 1) {
					await this.RoomListApi();
				}
			},
			async RecommandRoomListApi() {
				let url = "/room/recommendByUser";

				this.$comm.ShowLoading();
				let {
					success,
					data: {
						list
					}
				} = await this.$http.PostAsync(url, {});
				if (success == true) {
					this.recRoomItem = list;
				}
				uni.hideLoading()
			},

			async RoomListApi() {
				let url = "/room/findByModal";

				this.$comm.ShowLoading();
				let {
					success,
					data: {
						list
					}
				} = await this.$http.PostAsync(url, {});
				if (success == true) {
					this.roomItem = list;
				}
				uni.hideLoading()
			},


			async ToDetail(id) {
				if (this.$comm.CheckToken()) {

					uni.navigateTo({
						url: `/pages/room/room-detail?id=${id}`
					})

				}
			},

		}
	}
</script>

<style scoped>
	.publish {
		display: flex;
		align-items: center;
		justify-content: center;
		background-color: #1CBBB4;
		width: 100upx;
		height: 100upx;
		border-radius: 50%;
		position: fixed;
		right: 30upx;
		bottom: 150upx;
	}
</style>
